export default {
  props: {
    type: {
      type: String,
      default: 'month'
    }
  },
  computed: {
    dateList() {
      var arr = this.getArr(this.type)
      return arr
    }
  },
  data() {
    return {
      nowIndex: 6
    }
  },
  methods: {
    getArr(type) {
      var timeArr = []
      if(type == 'month'){
        timeArr = [
          {
             name: '6月前',
            isActive: true,
            checked: false,
            value: -6
          },
          {
            name: '5月前',
            isActive: true,
            checked: false,
            value: -5
          },
          {
            name: '4月前',
            isActive: true,
            checked: false,
            value: -4
          },
          {
            name: '3月前',
            isActive: true,
            checked: false,
            value: -3
          },
          {
            name: '2月前',
            isActive: true,
            checked: false,
            value: -2
          },
          {
            name: '上月',
            isActive: true,
            checked: false,
            value: -1
          },
          {
            name: '本月',
            isActive: true,
            checked: true,
            value: 0
          }
        ]
      }
      if(type == 'day'){
        timeArr = [
          {
            name: '6天前',
            isActive: true,
            checked: false,
            value: -6
          },
          {
            name: '5天前',
            isActive: true,
            checked: false,
            value: -5
          },
          {
            name: '4天前',
            isActive: true,
            checked: false,
            value: -4
          },
          {
            name: '3天前',
            isActive: true,
            checked: false,
            value: -3
          },
          {
            name: '2天前',
            isActive: true,
            checked: false,
            value: -2
          },
          {
            name: '昨天',
            isActive: true,
            checked: false,
            value: -1
          },
          {
            name: '今天',
            isActive: true,
            checked: true,
            value: 0
          }
        ]
      }
      return timeArr;
    },
    getMonth(month) {
      var time = new Date();
      time.setMonth(time.getMonth() + month);//设置month月后的时间
      var y = time.getFullYear();
      var m = time.getMonth() + 1;//获取当前月份
      var d = time.getDate();
      if(m<10){
        m = "0"+m
      }
      return y + "-" + m ;
    },
    addDate(days) {
      var d = new Date();
      d.setDate(d.getDate() + days);
      var m = d.getMonth() + 1;
      if(m < 10){
        m='0'+m
      }
      var day = d.getDate()
      if(day < 10){
        day='0'+day
      }
      return d.getFullYear() + '-' + m + '-' + day;
    },
    choseTime(item, index) {
      if(this.type == 'month'){
        console.log(this.getMonth(item.value))
      }
      if(this.type == 'day'){
        console.log(this.addDate(item.value))
      }
      this.nowIndex = index
    }
  }
}
